<template>
  <div v-loading="isLoading">

    <div class="btn-right">
      <el-button class="edit-btn" size="mini" @click="handleDelete()" plain>
        <i class="el-icon-delete delete"></i>删除</el-button>
      <el-button class="edit-btn" size="mini" @click="jumpPageEditForm(1)" plain>编辑表单</el-button>
      <el-button class="edit-btn" size="mini" @click="jumpPageEditForm()" plain>新建表单</el-button>
    </div>
    <fm-generate-form class="form-box" :data="jsonData" v-if='isShowForm' ref="generateForm">
    </fm-generate-form>
    <section class="export-btn">
      <el-button type="primary" size="default" @click="handleOutPut">
        导出txt
      </el-button>
    </section>

  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      return {
        detail: {},
        jsonData: {},
        isShowForm: false,
        detailid: 0,
        isLoading: false,
      }
    },
    watch: {
      '$route'(value) {
        value && this.getFormDetail()
      }
    },
    computed: {
      formid() {
        return this.$route.query.id
      }
    },
    created() {
      // this.handleOutPut();
      this.getFormDetail();

    },
    methods: {
      async getFormDetail() {
        this.isLoading = true;
        let resdata = await this.$http.post('/form/getFormDetail', {
          id: this.$route.query.id,
        }).catch(d => this.isLoading = false)
        this.isLoading = false;
        if (!resdata || !resdata.data) return
        this.detail = resdata.data;
        let json = decodeURIComponent(this.detail.formdata)
        this.jsonData = JSON.parse(json)
        this.$nextTick(() => {
          this.isShowForm = true;
          this.isLoading = false;
        })
      },

      handleDelete() {
        this.$confirm('确认删除<span style="color:red;">' + this.detail.formname + '</span>吗', '提示', {
          dangerouslyUseHTMLString: true,
        }).then(async data => {
          if (data === 'confirm') {
            return

            let data = await this.$http.post('/form/deleteForm', {
              id: this.formid
            })
            if (data.code === 1) {
              this.$message.success(data.message)
            } else {
              this.$message.error('删除失败')
            }
            this.getFormList();
          }
        })
      },
      jumpPageEditForm(isEdit) {
        let id = ``
        let name = ``
        if (isEdit) {
          id = this.formid
        }
        this.$router.push({
          path: '/admin/editForm',
          query: {
            id
          }
        })
      },
      handleOutPut(isPreview) {
        this.$refs.generateForm.getData().then((data) => {
          let outData = [];
          Object.keys(data).forEach(key => {
            if (!key.includes('demo') && !key.includes('date_pubish')) {
              //数组用、隔开
              let value = data[key] instanceof Array ? data[key].join('、') : data[key];
              let name = this.jsonData.list.find(item => item.model == key).name;
              value = value.replace(/\(前端\)/g, '').replace(/\(后端\)/g, '')
              outData.push(name + ':' + value + '\t\n');
            }
          })

          outData = outData.map((item, index) => {
            item = (index + 1) + '.' + item;
            return item;
          })

          outData.unshift(data['date_updatetime'] + `-网厅-正式环境更新说明：` + '\t\n')
          if (isPreview) {
            outData
            
            return
          }
          const element = document.createElement('a');
          element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(outData.join('')));
          element.setAttribute('download', data['date_pubish'])
          element.style.display = 'none'
          element.click()

        }).catch(e => {})
      }
    }
  }
</script>

<style lang="less" scoped>
  .btn-right {
    float: right;
  }

  .form-box {

    // height: 600px;
    overflow-y: scroll;
    margin: 50px auto;
    padding: 20px 40px;
    margin-bottom: 100px;
    // width: 80%;
    border: 1px solid #eee;

  }

  .export-btn {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 0;
    z-index: 9;
    left: 0;
    background: #fff;
    border-top: 1px solid #eee;

    .txt {}
  }
</style>


